<template>
  <div id="app">
    <i-layout>
      <i-layout-header>
        <Header></Header>
      </i-layout-header>

      <i-layout vertical>
        <i-layout-content>
          <TalentTable></TalentTable>
        </i-layout-content>
      </i-layout>

      <i-layout-footer>
        Just for fun
      </i-layout-footer>
    </i-layout>
  </div>
</template>

<script>
    import Vue from 'vue';
    import '@inkline/inkline/dist/inkline.css';
    import Inkline from '@inkline/inkline';

    Vue.use(Inkline);

    import Header from "./components/Header";
    import TalentTable from "./components/TalentTable";


    export default {
        name: 'app',
        components: {

            Header,
            TalentTable

        }
    }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }


</style>



